@charset "UTF-8";
.playMusic {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: 100;
  transition: all 0.5s;
  transform: translateY(100%);
  overflow: hidden; }
  .playMusic .bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #4d4d4d; }
    .playMusic .bg .before {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background: url("http://img0.imgtn.bdimg.com/it/u=3577671538,2909403054&fm=26&gp=0.jpg");
      background-size: 100% 100%;
      filter: blur(10px);
      opacity: 0.5; }
  .playMusic .tool {
    display: flex;
    position: absolute;
    justify-content: space-between;
    width: 100%;
    height: 60px;
    background-color: #fafafa;
    z-index: 1;
    transition: all 0.5s; }
    .playMusic .tool .item {
      display: flex;
      flex: 1;
      align-items: center;
      padding-left: 20px;
      height: 60px;
      background-color: rgba(0, 0, 0, 0); }
      .playMusic .tool .item .img {
        width: 50px;
        height: 50px;
        animation: rotate 10s linear infinite; }
        .playMusic .tool .item .img img {
          width: 100%;
          height: 100%;
          border: 1px solid #a39a9a;
          border-radius: 25px;
          background: url("http://img4.imgtn.bdimg.com/it/u=891174751,554927679&fm=26&gp=0.jpg");
          background-size: 100% 100%; }
      .playMusic .tool .item .text {
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 20px;
        font-size: 14px; }
        .playMusic .tool .item .text div {
          font-size: 12px;
          color: #888; }
    .playMusic .tool .btn {
      position: absolute;
      right: 0;
      top: 10px;
      display: flex;
      align-items: center;
      background-color: #fafafa;
      padding-left: 10px; }
      .playMusic .tool .btn .icon-bofang,
      .playMusic .tool .btn .icon-zanting {
        font-size: 30px;
        color: #d64637;
        margin-right: 20px; }
      .playMusic .tool .btn .icon-yinleliebiao {
        font-size: 40px;
        color: #d64637;
        margin-right: 10px; }
  .playMusic .audio {
    display: none; }
  .playMusic .all {
    position: absolute;
    width: 100%;
    height: 100%; }
    .playMusic .all .toolBox {
      display: flex;
      flex-direction: column;
      height: 100%;
      opacity: 0;
      transition: all .5; }
    .playMusic .all .am-icon-down {
      position: absolute;
      left: 15px;
      top: 10px;
      width: 35px;
      height: 35px;
      color: #e2e1e1;
      transition: all 0.5s; }
    .playMusic .all .title,
    .playMusic .all .singer {
      text-align: center;
      background-color: rgba(0, 0, 0, 0);
      font-size: 16px;
      margin-top: 20px;
      color: #e2e1e1; }
    .playMusic .all .singer {
      color: #afa39f;
      font-size: 14px;
      margin-top: 10px; }
    .playMusic .all .plate {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(255, 255, 255, 0.5);
      border-radius: 50%;
      margin: 0 auto 0;
      animation: rotate 10s linear infinite;
      transition: all 0.5s; }
      .playMusic .all .plate img {
        width: 95%;
        height: 95%;
        border-radius: 50%; }
    .playMusic .all .lycBrief {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 90px;
      width: 70%;
      margin: 0 auto;
      transition: all 0.5s; }
      .playMusic .all .lycBrief p {
        text-align: center;
        line-height: 25px;
        font-size: 16px;
        color: white; }
    .playMusic .all .lycBox {
      position: relative;
      overflow: hidden;
      flex: 1;
      margin: 30px 0; }
    .playMusic .all .allLyc {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.5s;
      text-align: center;
      padding: 0 8%;
      line-height: 18px;
      color: #bdbdbd;
      font-size: 14px; }
      .playMusic .all .allLyc p {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50px; }
      .playMusic .all .allLyc p.current {
        line-height: 25px;
        height: auto;
        margin: 15px 0;
        color: #ffffff;
        font-size: 18px; }
    .playMusic .all .allTool {
      height: 120px;
      width: 100%; }
      .playMusic .all .allTool .progressBar {
        display: flex;
        align-items: center;
        width: 70%;
        margin: 0 auto;
        color: #e2e1e1;
        justify-content: center; }
        .playMusic .all .allTool .progressBar .am-progress-outer {
          flex: 1;
          height: 4px;
          margin: 0 10px;
          background-color: rgba(0, 0, 0, 0.2); }
          .playMusic .all .allTool .progressBar .am-progress-outer .am-progress-bar {
            border-color: #d44439; }
        .playMusic .all .allTool .progressBar .progressBox {
          display: flex;
          width: 60%;
          align-items: center;
          height: 24px;
          margin: 0 5px; }
          .playMusic .all .allTool .progressBar .progressBox > div {
            width: 100%;
            height: 4px;
            background-color: rgba(255, 255, 255, 0.4);
            border-radius: 2px; }
          .playMusic .all .allTool .progressBar .progressBox .progress {
            position: relative;
            height: 4px;
            width: 0;
            background-color: #d4473e;
            border-radius: 2px;
            max-width: 100%; }
            .playMusic .all .allTool .progressBar .progressBox .progress::before {
              position: absolute;
              right: -4px;
              top: -2px;
              content: '';
              width: 8px;
              height: 8px;
              border-radius: 50%;
              background-color: #fff; }
      .playMusic .all .allTool .control {
        display: flex;
        width: 85%;
        margin: 20px auto 0; }
        .playMusic .all .allTool .control i {
          text-align: center;
          flex: 1;
          font-size: 35px;
          color: #e2e1e1; }

