body {
    background: url("http://img4.imgtn.bdimg.com/it/u=2007870969,2735994731&fm=26&gp=0.jpg");
    background-size: 100% 100%;
}
.animation.icon-yinle {
    position: fixed;
    top: 200px;
    left: 200px;
    z-index: 999;
    font-size: 14px;
    transition: all 1.5s;
    color: #d4473e;
    animation: rotate 3s linear infinite;
    opacity: 1;
}


@keyframes rotate {
    0%{
        transform: rotate(0)
    }
    100%{
        transform: rotate(360deg)
    }
}